import React,{useState} from 'react';
import { NavLink } from 'react-router-dom';
import './style.css';
import Card from '../UI/Card';

const Header = props =>{

    const [search,setSearch] = useState(false);

    const openSearch = () => {
        setSearch(true);
    }

    const submitSearch = (e) => {
        e.preventDefault();
        alert('Searched')
    }

    const searchClass = search?'searchInput active':'searchInput';

    return(
        <header className="header">
            <nav>
                <ul className="headerMenu">
                    <li><NavLink to="/">Home</NavLink></li>
                    <li><NavLink to="/about-us">About Us</NavLink></li>
                    <li><NavLink to="/post">Post</NavLink></li>
                    <li><NavLink to="/contact-us">Contact Us</NavLink></li>
                </ul>
                <div className="socialMedia">
                    <ul>
                        <li><a href="#" className="fa fa-linkedin"></a></li>
                        <li><a href="#" className="fa fa-facebook-square"></a></li>
                        <li><a href="#" className="fa fa-twitter-square"></a></li>
                        <li><a href="#" className="fa fa-behance-square"></a></li>
                    </ul>
                </div>
            </nav>
            <div className="hero">
                <Card><img src={require('../../assets/images/logo.png')} alt="logo" className="logo" /></Card>
            </div>
            <div className="navigation">
                <ul className="navMenu">
                    <li><a href="#">Home</a></li>
                    <li><a href="#">Features</a></li>
                    <li><a href="#">Learn Blogging</a></li>
                    <li><a href="#">Documentation</a></li>
                </ul>
                <div className="search">
                    <form onSubmit={submitSearch}>
                        <input className={searchClass} type="text" placeholder="Search here..." /><i onClick={openSearch} className="fa fa-search searchIcon" />
                    </form>
                </div>
            </div>
        </header>
    );
}

export default Header;